import React, { Fragment } from "react"; 
import MetaTags from "react-meta-tags";
import LayoutOne from "../layouts/LayoutOne";
import AboutContent from '../components/AboutContent'; 
import Team from '../components/Team'; 
import WorkProcess from '../components/WorkProcess'; 
import Testimonial from '../components/Testimonial';  
 
const About = () => {
 
    return(
        <Fragment>
            <MetaTags>
              <title>FuodBorne | About</title>
              <meta
                name="description"
                content="Organic Food React JS Template."
              />
            </MetaTags>
            <LayoutOne>
        

           <div className="about-page">
                {/*====================  breadcrumb area ====================*/}
                <header id="page-top" className="blog-banner"> 
                    <div className="container" id="blog">
                        <div className="row blog-header text-center">
                            <div className="col-sm-12"> 
                                <h3>About cccccc</h3>
                                <h4><a href="index.html"> Home </a> &gt; About Us </h4>
                            </div>
                        </div> 
                    </div> 
                </header>
                {/*====================  End of breadcrumb area  ====================*/} 

                {/*====================  About area ====================*/}

                    <AboutContent />

                {/*==================== End:  About area ====================*/}


                {/*==================== Team Mamber area  ====================*/} 

                    <Team />

                {/*====================  End Team Mamber area  ====================*/} 

            
                {/*====================  Working Process area  ====================*/}

                    <WorkProcess />

                {/*====================  End: Working Process area  ====================*/} 


                {/*====================  Client Satisfait area  ====================*/} 

                    <Testimonial />

                {/*====================  End: Client Satisfait area  ====================*/} 

            </div>

            </LayoutOne>
        </Fragment>
            
    ); 
};

export default About;   